@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    font: 400 14px Roboto;
    background: #c2c1c2;
    color: #022B3A;
    min-height: 100vh;
}

.wrapper {
    padding: 2%;
    margin: 14px;
    margin-top: 50px;
    margin-bottom: 100px;
    transition: 0.5s;
    background-color: #efefef;
    height: 100%;
    bottom: 0;
    min-height: 100vh;
    position: relative;
}

.text-link, .text-link:focus, .text-link:hover, .text-link:visited, .text-link:link, .text-link:active {
    color: inherit;
    text-decoration: inherit;
}

b {
    color: #DE4842;
}
.sidenav{
    color: #efefef;
    background: rgba(2,43,58, 0.95);
    height: 100%;
    width: 200px;
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    overflow-x: hidden;
    padding-top: 10px;
    display:block;

    transition: 0.3s;
}

.navitem {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    display: list-item;
    border: 10px;

    transition: 0.3s;

    position: relative;
}

.navitems {
    display: block;
    flex-direction: column;
    justify-content: space-between;
    padding-top: 35px;

    transition: 0.3s;
}

.navitem:hover {
    background-color: rgba(255,255,255,0.1);
    transition: 0.3s;
}

.navitem:not(:hover) {
    transition: 0.3s;
}

.sidenav-foot {
    bottom: 0;
    position: absolute;
    
    transition: 0.3s;
}

.sidenav-foot h3:hover {
    font-size: 3em;
    
    transition: 0.5s;;
}

.sidenav-foot h3:not(:hover) {
    
    transition: 0.5s;
}

.collapse-btn {
    background-color: rgba(2,43,58, 0.95);
    border-radius: 0;
    border-bottom-right-radius: 40%;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    transition: 0s;
    margin-left: 200px;

    transition: 0.3s;
}

.collapsed.sidenav {
    width: 0;
}


.collapsed.collapse-btn {
    margin-left: 0;
}

.collapsed {
    display:block;
}

.collapsed .nav-icon{
    transition: 3s;
}

.nav-icon{
    position: absolute;
    margin-top: 5px;
}

.nav-name {
    margin-left: 24px;
}
.page-header h1 {
    color: #022B3A;
    font-size: 15px;
}

.page-header {
    float: right;
    top: 0;
    right: 0;
    position: absolute;
    
    margin-top: 3px;
    margin-right: 10px;

    border-bottom: 100px;
}
img {
    max-width: 100%;
    width: 500px;
}

.post-container {
    margin-bottom: 20px;
}

.post-content {
    max-width: 100%;
    margin: 30px;
    color: #022B3A;
}

.post-header {
    margin-left: 10px;
    color: #022B3A;
}

.palette {
    display: inline-flex;
    position: relative;
    min-width: 100%;
    min-height: 50px;

    margin-bottom: 40px;
}

.color-div {
    display: flex;
    flex-direction: column-reverse;
    width: 100%;
    height: 100%;

    border: 0px;
    border-top: 2px;
    border-bottom: 2px;
    border-style: solid;
}

.palette-name {
    font-size: 18px;
    font-weight: 300;
}
.section-title {
    font-size: 35px;
    font-weight: 500;
    color: #DE4842;
    display: block;
    flex-direction: row;

    margin-left: 10px;
}

.section-body {
    margin-top: 20px;
    font-size: 15px;
    text-align: justify;
    margin: 10px;
}


.section-icon {
    margin-bottom: 6px;
}

.section {
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
}

.sections-wrapper {
    display: flex;
    flex-direction: row;
}

.section-img {

    width: 100%;
    display: block;
    flex-direction: column;
    text-align: center;

    margin-bottom: 5px;
    padding: 20px;
    overflow: auto;
}

.section-img p {
    text-align: justify;
}


.figure {
    border: solid #022B3A 2px;
    margin-bottom: 10px;
}
.project-card {
    max-width: 18rem;
    width: 100%;
    align-items: center;
    background-color: rgb(194, 193, 194, 0.2);
    min-height: 38rem;
    margin: 10px;

    display: flex;
    flex-direction: column;

    border-radius: 2%;

    padding-bottom: 40px;

    margin-bottom: 30px;
}

.project-img {
    min-width: 18rem;
    height: 18rem;

    border-top-left-radius: 2%;
    border-top-right-radius: 2%;
}

.project-title {
    font-size: 25px;
    margin-top: 15px;
    font-weight: 500;
    color: #DE4842;

    text-align: center;
}

.project-body {
    align-items: space-around;
    justify-content: space-between;

    text-align: center;
    font-size: 15px;

}

.project-btn.project-btn {
    bottom: 0;
    width: 100%;
    color: #EFEFEF;
    background-color: #DE4842;
    font-size: 20px;
    font-weight: 700;

    position: absolute;
}

.project-btn:hover {
    background-color: rgba(222, 72, 66, 0.9); 
    transition: 0.3;
}

.project-content {
    display: block;
}

.project-card hr {
    border: #DE4842;
    background-color: #DE4842;
    width: 60%;
    height: 1px;
    margin-bottom: 20px;
}
.footer {
    background-color: #c2c1c2;
    bottom: 0;
    left: 0;
    width: 100%;
    position: relative;
    text-align: center; 

    font-weight: 800;
    color: #022B3A;

    justify-content: space-around;
    align-content: space-around;

    display: flex;
    flex-direction: row;

    padding-right: 10px;
}

.footer-icon {
    margin-bottom: 20px;
    justify-content: space-between;

    display: block;
    margin-left: auto;
    margin-right: auto;

    flex-direction: column;
}
